// 제목 폰트
.akaya-kanadaka-regular {
  font-family: "Akaya Kanadaka", "system-ui";
  font-weight: 400;
  font-style: normal;
}

.salsa-regular {
  font-family: "Salsa", "cursive";
  font-weight: 400;
  font-style: normal;
}

// 내용 폰트
@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

.gowun-dodum-regular {
  font-family: "Gowun Dodum", "sans-serif";
  font-weight: 400;
  font-style: normal;
}

.gowun-batang-regular {
  font-family: "Gowun Batang", "serif";
  font-weight: 400;
  font-style: normal;
}

.noto-serif-kr-regular {
  font-family: "Noto Serif KR", "serif";
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

.noto-sans-kr-regular {
  font-family: "Noto Sans KR", "sans-serif";
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}


$pointColor: #41A8BF;
$bgColor: #FDFDFD;

.Project {
  display: flex;

  .project_list {
    width: 55%;
    border-right: 2px solid $pointColor;
    height: calc(100vh - 196px);
    overflow-y: auto;

    &::-webkit-scrollbar {
      display: none;
    }

    .pj {
      cursor: pointer;
      width: 100%;
      line-height: 188px;
      padding-left: 5%;
      font-family: "Pretendard-Regular", "sans-serif";
      font-size: 3em;
      text-shadow:
        -1px -1px 0 $pointColor,
        1px -1px 0 $pointColor,
        -1px 1px 0 $pointColor,
        1px 1px 0 $pointColor;

      &:not(.on) {color: $bgColor;}

      &.on {color: $pointColor;}

      &:not(:last-child) {border-bottom: 2px solid $pointColor;}
    }
  }

  .project_inf {
    // background-color: aquamarine;
    width: 45%;
    height: calc(100vh - 196px);

    .project_img {
      width: 100%;
      height: 70%;
      border-bottom: 2px solid $pointColor;
      // background-color: burlywood;

      img {
        cursor: pointer;
        width: 100%;
        height: 100%;
        object-fit:cover;
        object-position: top;
        transition: object-position 3s ease-in-out;

        &:hover {
          object-position: bottom;
        }
      }
    }

    .project_contents {
      width: 100%;
      // height: 20%;
      padding: 10px;
      // background-color: cadetblue;

      .project_tools {
        display: flex;
        flex-wrap: wrap;
        padding: 5px 5px;
        
        li {
          // border: 1px solid $pointColor;
          border-radius: 7px;
          background-color: $pointColor;
          padding: 5px 13px;
          margin: 3px 5px;
          color: $bgColor;
          font-size: 0.9em;
        }
      }

      .project_tit {
        padding: 10px 10px;
        color: $pointColor;

        h3 {
          font-family: "Gowun Dodum", "sans-serif";
          font-size: 1.5em;
        }
        
        p {
          font-family: "Gowun Dodum", "sans-serif";
          font-size: 1.1em;
        }

        h4 {
          padding-top: 2%;
          // font-family: "Gowun Dodum", "sans-serif";
          font-size: 1.1em;
          font-weight: 500;
        }
      }
    }
  }
}