// 제목 폰트
.akaya-kanadaka-regular {
  font-family: "Akaya Kanadaka", "system-ui";
  font-weight: 400;
  font-style: normal;
}
.salsa-regular {
  font-family: "Salsa", "cursive";
  font-weight: 400;
  font-style: normal;
}
.outfit-regular {
  font-family: "Outfit", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}
.outfit-semibold {
  font-family: "Outfit", sans-serif;
  font-weight: 600;
}
// 내용 폰트
@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}
.gowun-dodum-regular {
  font-family: "Gowun Dodum", "sans-serif";
  font-weight: 400;
  font-style: normal;
}
.gowun-batang-regular {
  font-family: "Gowun Batang", "serif";
  font-weight: 400;
  font-style: normal;
}
.noto-serif-kr-regular {
  font-family: "Noto Serif KR", "serif";
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}
.noto-sans-kr-regular {
  font-family: "Noto Sans KR", "sans-serif";
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}


$pointColor: #41A8BF;
$bgColor: #FDFDFD;

.About {
  height: calc(100vh - 196px);
  overflow-y: auto;

  &::-webkit-scrollbar {
    display: none;
  }
}

.intro {
  padding: 10% 20px;
  border-bottom: 2px solid $pointColor;

  div {
    font-family: "Akaya Kanadaka", "system-ui";
    font-size: 8em;
    text-align: center;
    color: $bgColor;
    text-shadow:
      -1px -1px 0 $pointColor,
      1px -1px 0 $pointColor,
      -1px 1px 0 $pointColor,
      1px 1px 0 $pointColor;

    span {
      font-family: "Akaya Kanadaka", "system-ui";
      color: $pointColor;
    }
  }
}

.main {
  display: flex;
  // width: 100%;
  min-height: 0;

  .side {
    padding: 20px;
    min-width: 278px;
    // height: calc(100vh - 196px);
    border-right: 2px solid $pointColor;

    ul {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      line-height: 25px;
      height: 100%;

      li {        
        color: $pointColor;

        &:first-child {
          font-family: "Akaya Kanadaka", "Salsa";
          font-size: 1.8em;
          padding-bottom: 10%;
        }

        &:not(:first-child) {
          font-size: 0.9em;

          span {
            font-weight: 600;
          }
        }
      }
    }
  }

  .content {
    flex: 1;
    // width: 100%;
    height: calc(100vh - 196px);
    min-height: 0;
    overflow-y: auto;

    &::-webkit-scrollbar {
      display: none;
    }

    .about_tit {
      text-align: center;
      width: 100%;
      line-height: 78px;
      font-family: "Akaya Kanadaka", "Salsa";
      font-size: 1.8em;
      color: $pointColor;
      border-bottom: 2px solid $pointColor;

      &:nth-of-type(2) {border-top: 2px solid $pointColor;}
    }

    .skills {
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      // height: 400px;

      > li {
        width: 16.666666666666666666666666666667%;
        padding: 20px;
        color: $pointColor;
        text-align: center;

        @for $i from 1 through 6 {
          &:nth-of-type(#{$i}) {
            border-bottom: 2px solid $pointColor;
          }
        }

        &:not(:nth-of-type(6), :nth-of-type(12)) {
          border-right: 2px solid $pointColor;
        }

        &:last-child {
          background-color: $pointColor;
        }

        .my_skill {
          fill: $pointColor;
        }
      }
    }
  }
}